
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import AjaxPlugin from 'vux/src/plugins/ajax/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'

import { getSingleUserInfo, fetch, setUserInfo } from '@/api/base'
export default {
components: {
XTextarea,
Group,
Box,
XHeader,
XButton,
AjaxPlugin,
ToastPlugin
},
data () {
return {
name: getSingleUserInfo('user_curatorName')
}
},
methods: {
changeName () {
let me = this
let reg = /^([\u4e00-\u9fa5]{1,4})$/
if (reg.test(me.name)) {
fetch('/api/v1/agents/chessName', {name: me.name}, response => {
let data = response.data
let msg = response.data.msg || response.error || '未知错误'
me.$vux.toast.text(msg, 'middle')
if (data.code !== 0) return false
setUserInfo('user_curatorName', me.name)
this.$router.go(-1)
}, error => {
console.log(error)
})
} else {
return this.$vux.toast.text('请输入4位汉子', 'middle')
}
}
}
}



